
import bg from '../../images/slider-2.jpeg'
export default function Header({catId}) {

  return (
    <div className='row'>
        <div className="col-md-2">
        <aside style={{ backgroundColor: '#F8F9FA',height:'40vh' }} className=' p-5 d-flex justify-content-center flex-column'>
          <a href="#products" className='nav-link mt-2'>Products</a>
          <a href="" className='nav-link mt-2'>Brands</a>
          <a href={`#${catId}`} className='nav-link mt-2'>Categories</a>
        </aside>
      </div>
      <div className="col-md-10">
        <header style={{height:'40vh',backgroundImage:`url(${bg})`,backgroundSize:'cover'}}>
        </header>
      </div>
    </div>
  )
}
