import React, { useContext, useEffect, useState } from 'react'
import useApi from '../../Hooks/useApi'
import { Link, useNavigate } from 'react-router-dom'
import { CartContext } from '../../Context/CartContext'
import { toast } from 'react-hot-toast'

export default  function Products() {
  let navigate = useNavigate()
  const {addToCart} = useContext(CartContext)
  const [currentPage ,setCurrentPage]= useState(1)
  const pages=[1,2]
  const { products } =  useApi(`https://ecommerce.routemisr.com/api/v1/products?page=${currentPage}`)

 

 async function addToCartFun(id)
  { 
  if(localStorage.getItem('token'))
  {
    let data =  await addToCart(id)
    if(data.status == 'success')
     {
       toast.success('your product added successful')
     }
     else
     {
       toast.error(data.message)
     }
  }
  else
    navigate('/login')
  }

  return ( 
    <div className="row py-5 g-4" id="products">
      {products.length > 0 ? products.map((product) => <div key={product._id} style={{ cursor: 'pointer' }} className='col-md-3'>
        <div className="productItem p-3 shadow-sm text-center" style={{ backgroundColor: '#F8F9FA' }}>
         <Link to={product._id} style={{textDecoration:'none',color:'unset'}}>
         <img src={product.imageCover} className='w-50' alt="" />
         </Link>
          <p style={{ color: '#1CB21C' }} className='my-2'>{product.title.split(" ").slice(0, 2).join(" ")}</p>
          <span>{product.ratingsAverage} <i className='fa-solid fa-star text-warning'></i></span>
          <button className='btn btn-outline-success d-block mx-auto my-2 opacity-1 ' onClick={()=>{addToCartFun(product._id)}}>Add to cart</button>
    
        </div>
      </div>) : 'loading'}

     
    </div>
  )
}
