import React from 'react'
import useApi from '../../Hooks/useApi'
import Slider from 'react-slick';

export default function Categories() {

  const {categories} = useApi('https://ecommerce.routemisr.com/api/v1/categories')

  let settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 8,
    slidesToScroll: 1,    
  };
  return (
    <div className='py-4' id='categories'>
      
        {categories.length>0? <Slider {...settings} >
         {categories.map((ele)=><div key={ele._id}>
          <img src={ele.image}  style={{height:'150px',objectFit:'cover'}}  alt={ele.name} className='w-100'/>
          <p>{ele.name}</p>
         </div>)}  
        </Slider>:'loading'}
      
    </div>
  )
}
